.singleTripContainer {
    width: 100%;
    margin: 2vh 0vw;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: rgba(11,11,59,.75);
    border: 2px solid whitesmoke;
    color: whitesmoke;
    border-radius: 10px;
    padding: 2vh 1vw;
    font-size: 1.2em;
}

.singleTripContainer strong {
    font-weight: bold;
    font-size: 1.1em;
}

.singleTripContainer .tripNumero {
    width: 7%;
    font-size: .75em;
}

.singleTripContainer .tripFechaHora {
    width: 26%;
    display: flex;
    align-items: center;
    padding: 0vh 1vw;
}

.singleTripContainer .tripFechaHora span {
    margin-right: 1vw;
}

.singleTripContainer .tripOrigen, .tripDestino {
    width: 20%;
}

.singleTripContainer .tripTipoEjecutivo {
    width: 10%;
    text-align: center;
    background-color: blue;
    font-weight: bold;
    letter-spacing: 10px;
}

.singleTripContainer .tripTipoPartner {
    width: 10%;
    text-align: center;
    background-color: red;
    font-weight: bold;
}

.singleTripContainer .tripTarifa2 {
    width: 10%;
    text-align: center;
    font-weight: bold;
    color: #FFFF00;
    
}

.singleTripContainer .tripPlusInfo {
    width: 4%;
    text-align: right;
    font-size: 1.8em;
    color: #CEF6CE;
}

.singleTripContainer .tripPlusInfo:hover {
    cursor: pointer;
}

.moreInfoContainer {
    width: 100%;
    margin-top: 4vh;
    display: flex;
    flex-direction: column;
}

.moreInfoContainer>* {
    margin: 1vh 0vw;
}

.moreInfoContainer .moreInfoPasajero .moreInfoNombreTelefono {
    margin-left: 4vw;
}

.moreInfoContainer .moreInfoLine {
    display: flex;
    flex-wrap: wrap;
}

.moreInfoContainer .moreInfoLine p {
    width: 20%;
    font-weight: bold;
}

.moreInfoContainer .moreInfoParticularidades {
    display: flex;
    justify-content: center;
}

.moreInfoContainer .moreInfoEmisorYBotones {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 4vh;
}

.moreInfoContainer .moreInfoEmisorYBotones .moreInfoEmisor span {
    background-color: white;
    color: red;
    border-radius: 10px;
    padding: 1vh 1vw;
    font-weight: bold;
    margin-left: 2vw;
}

.moreInfoContainer .moreInfoEmisorYBotones .moreInfoBotones {
    width: 50%;
    display: flex; 
    justify-content: space-around;
    align-items: center;
}

.moreInfoContainer .moreInfoEmisorYBotones .moreInfoBotones button {
    width: 40%;
}

.moreInfoContainer .badge {
    font-weight: bold;
}

.viajeEliminado {
    font-weight: bold;
    letter-spacing: 1.3px;
    background-color: red;
    color: whitesmoke;
    padding: 1vh 5vw;
}

.viajeAceptado {
    letter-spacing: 1.2px;
    margin: 2vh 0vw;
    line-height: 1.5em
}

.viajeAceptado span {
    background-color: white;
    color: red;
    border-radius: 10px;
    padding: 1vh 1vw;
    font-weight: bold;
    margin-left: 2vw;  
}

/* MEDIAQUERIES */
@media (max-width: 1050px) {
    .singleTripContainer .tripNumero {
        width: 20%;
        font-size: 1em;
        margin: 1vh 0vw;
    }
    .singleTripContainer .tripFechaHora {
        width: 80%;
        margin: 1vh 0vw;
    }

    .singleTripContainer .tripOrigen, .tripDestino {
        width: 50%;
        margin: 1vh 0vw;
    }

    .singleTripContainer .tripTipoEjecutivo {
        width: 40%;
        margin: 1vh 0vw;
        background-color: transparent;
        color: #58FAF4;
    }

    .singleTripContainer .tripTipoPartner {
        width: 40%;
        margin: 1vh 0vw;
        background-color: transparent;
        letter-spacing: 7px;
        color: #FA5858;
    }

    .singleTripContainer .tripTarifa2 {
        width: 40%;
        margin: 1vh 0vw;
    }

    .singleTripContainer .tripPlusInfo {
        width: 20%;
        margin: 1vh 0vw;
    }

    .moreInfoContainer .moreInfoLine p {
        width: 30%;
        font-weight: bold;
    }
    
}

@media (max-width: 700px) {
    .singleTripContainer .tripOrigen, .tripDestino {
        width: 100%;
    }

    .moreInfoContainer .moreInfoPasajero .moreInfoNombreTelefono {
        margin-left: 10vw;
    }

    .moreInfoContainer .moreInfoLine.observaciones p {
        width: 100%;
        font-weight: bold;
    }

    .moreInfoContainer .moreInfoEmisorYBotones {
        flex-direction: column;
        margin-top: 3vh;
    }

    .moreInfoContainer .moreInfoEmisorYBotones .moreInfoBotones {
        width: 100%;
        margin-top: 3vh;
    }

    .moreInfoContainer .moreInfoParticularidades {
        display: flex;
        flex-direction: column;
    }

    .moreInfoContainer .moreInfoParticularidades * {
        margin: 0vh auto 1vh auto;
        width: 75%;
    }

    .moreInfoContainer .moreInfoParticularidades span:last-child {
        margin: 0vh auto 0vh auto;
    }

}