html * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    background-image: url('../assets/fondoCielo.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    background-repeat: no-repeat;
    font-family: 'Raleway', sans-serif;
}

body button, input, select {
    font-family: 'Raleway', sans-serif;
}

/* HEADER */
header {
    width: 100%;
    background-color: #0B0B3B;
    border-bottom: 2px solid whitesmoke;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0vh 2vw;
    font-family: 'Montserrat', sans-serif;
}

header .logo-brand {
    display: flex;
    align-items: center;    
}

header .logo-brand .imagen {
    width: 7vw;
    min-width: 80px;
}

header .logo-brand h3 {
    color: whitesmoke;
    font-size: 2.3em;
    font-weight: bold;
    margin-left: 2vw;
}

header .menu ul * {
    color: whitesmoke;
    margin-left: 3vw;
    font-size: 1.2em;
}

header .menu ul *.active {
    color: #0080FF;
}

header .menu ul *:hover {
    border-bottom: 2px solid whitesmoke;
    padding-bottom: 1vh;
}

/* MAIN CONTAINER */
.mainContainer {
    width: 95%;
    margin: 1vh auto;
}

/* LOGIN CONTAINER */
.loginContainer {
    width: 90%;
    margin: 5vh auto;
    max-width: 500px;
    text-align: center;
    background-color: rgba(255, 255, 255, .6);
    border-radius: 15px;
    padding: 1vh 5vw;
}

.loginContainer form h5 {
    font-weight: bold;
}

.loginContainer form .input-field.mt2vh {
    margin-top: 3vh;
}

/* NEW TRIP CONTAINER */
.newTripContainer {
    width: 100%;
}

/* AMIGOS CONTAINER */
.amigosContainer {
    width: 80%;
    margin: 4vh auto;
    text-align: center;
    background-color: rgba(255,255,255,.5);
    padding: 5vh 3vw 3vh 3vw;
    border-radius: 20px;
}

.amigosContainer .titulo {
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    color: blue;
    letter-spacing: 1.5px;
}

.amigosContainer p:nth-child(2) {
    width: 90%;
    margin: 4vh auto;
}

.amigosContainer .nombresAmigos label {
    margin: 2vh 0vw;
}

.amigosContainer .amigosBotones button {
    width: 50%;
    margin: 1vh auto; 
}

/* PENDING CONTAINER - HISTORICO CONTAINER */
.pendingContainer, .historicoContainer {
    width: 100%;
}

.historicoContainer .historicoMenu {
    width: 90%;
    margin: 4vh auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.historicoContainer .historicoMenu>* {
    width: 30%;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 1.4px;
    font-size: 1.2em;
}

/* MediaQueries */
@media (max-width: 1050px) {
    header {
        flex-direction: column;
    }

    header .menu ul {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    header .menu ul * {
        margin: 1vh 3vw 1vh 3vw;
    }

    header .menu ul *:hover {
        border-bottom: none;
        padding-bottom: 0vh;
        color: #0080FF;
        }
    
    .historicoContainer .historicoMenu {
        flex-direction: column;
    }
    .historicoContainer .historicoMenu>* {
        margin: 0vh auto 2vh auto;
        width: 90%; 
    }
}