.tripForm {
    width: 100%;
    border-radius: 15px;
    background-color: rgba(255,255,255,.3);
    margin-top: 2vh;
    padding: 2vh 2vw;
}

.tripForm select {
    padding: 1vh 1vw;
    background: transparent;
    border-color:rgba(8,8,138,.2);
}

.tripForm select option {
    padding: 1vh;
    background: transparent;
}

.tripForm .row {
    margin: 0;
}

.tripForm .row.botones {
    text-align: center;
    margin-top: 4vh;
}

.tripForm .row.botones button {
    min-width: 50%;
}
